import React , { Fragment, useEffect } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { getCurrentProfile, deleteAccount } from '../../actions/profile'
import { Link } from 'react-router-dom'
import DashboardActions from './DashboardActions'
import Experience from './Experience'
import Education from './Education'

const Dashboard = ({ getCurrentProfile, deleteAccount, auth: { user }, profile: {profile, loading } }) => {
    useEffect(() => {
        getCurrentProfile()
    }, [getCurrentProfile])
    
    return (<Fragment>
        <h1 className="large text-primary">Welcome To Dashboard</h1>
        <p className="lead">
            <i className="fas fa-user"></i> Hello { user && user.name }. Nice To Meet You!!</p>
        {profile !== null ? (
            <Fragment><DashboardActions /> 
            <Experience experience={profile.experience} />
            <Education education={profile.education} />
            <div className="my-2"> 
            <button className="btn btn-danger" onClick={() => deleteAccount()}> 
            <i className="fas fa-user-minus"></i> Delete Account 
            </button> 
            </div> </Fragment>
        ) : (
            <Fragment><p>You don't have any profile.</p>
            <Link to='/create-profile' className="btn btn-primary my-1"> Create Profile</Link> </Fragment>
        )}
    </Fragment>
    )
}

Dashboard.propTypes = {
    getCurrentProfile: PropTypes.func.isRequired,
    deleteAccount: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    profile: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    auth: state.auth,
    profile: state.profile
})

export default connect(mapStateToProps, { getCurrentProfile, deleteAccount })(Dashboard)